import { FC, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { useForm } from "react-hook-form";

import { makeStyles, Avatar, Button, Typography, CircularProgress } from "@material-ui/core";
import { Lock } from "@material-ui/icons";

import Input from "components/UI/Fields/Input";

import { isAuthorized } from "utils/isAuthorized";
import { login } from "store/ducks/auth/actionCreators";
import { selectStatus } from "store/ducks/auth/selectors";

const useStyles = makeStyles(theme => ({
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.primary.main,
  },
  form: {
    width: "100%",
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  spinner: {
    color: "white",
  },
}));

const Auth: FC = () => {
  const s = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();
  const AuthStatus = useSelector(selectStatus);

  const { control, handleSubmit, watch } = useForm();

  const [errors, setErrors] = useState<any>({});

  const email = watch("email");
  const password = watch("password");

  const onSubmit = (): void => {
    //        email@example.com
    if (!email || !/\S+@\S+\.\S+/.test(email)) {
      setErrors((prev: any) => ({
        ...prev,
        email: "  ",
      }));
      return;
    }

    if (!password) {
      setErrors((prev: any) => ({
        ...prev,
        password: "  ",
      }));
      return;
    }

    if (password.length < 8 || password.length > 50) {
      setErrors((prev: any) => ({
        ...prev,
        password: "   8 ,  50",
      }));
      return;
    }

    dispatch(login({ email, password, history }));
  };

  useEffect(() => {
    if (isAuthorized() || AuthStatus === "AUTHORIZED") history.push("/");
  }, [AuthStatus, history]);

  return (
    <>
      <Avatar className={s.avatar}>
        <Lock />
      </Avatar>
      <Typography variant="h5"></Typography>
      <form className={s.form} noValidate onSubmit={handleSubmit(onSubmit)}>
        <Input
          control={control}
          name="email"
          label="Email"
          type="text"
          autoComplete="email"
          error={!!errors.email}
          helperText={errors?.email}
          autoFocus
        />
        <Input
          control={control}
          name="password"
          label=""
          type="password"
          autoComplete="current-password"
          error={!!errors.password}
          helperText={errors?.password}
          autoFocus
        />

        <Button
          type="submit"
          fullWidth
          variant="contained"
          color="primary"
          className={s.submit}
          disabled={AuthStatus === "AUTHORIZING"}>
          {AuthStatus === "AUTHORIZING" ? <CircularProgress size={20} className={s.spinner} /> : ""}
        </Button>
      </form>
    </>
  );
};

export default Auth;
