import { SagaIterator } from "redux-saga";
import { call, put, takeEvery } from "redux-saga/effects";

import { errorHandler } from "utils/errorHandler";

import { API_AUTH } from "api/auth";
import { setAccessToken, setAuthStatus, AuthActionsTypes, ILoginAction } from "./actionCreators";
import { Status } from "./state";

export function* doLogin({ payload: values }: ILoginAction): any {
  try {
    const data = yield call(API_AUTH.login, values);
    const { history } = values;

    localStorage.setItem("token", data);
    yield put(setAccessToken(data));
    history.push("/");
  } catch (e) {
    yield put(setAuthStatus(Status.ERROR));

    if (e.response?.data?.error.code === 4009) {
      alert("   ");
    } else if (e.response?.data?.error.code === 4001) {
      alert("  ");
    } else if (e.response?.data?.error.code === 4003) {
      alert("   8 ,  50");
    } else {
      errorHandler(e);
    }
  }
}

export function* authSaga(): SagaIterator {
  yield takeEvery(AuthActionsTypes.LOGIN, doLogin);
}
