import { ITableHeader, ITableRow } from "components/UI/TableWrapper/types";
import { IPartner } from "store/ducks/owner/state";

export const getHeaders = (): ITableHeader[] => [
  { name: "" },
  { name: "id" },
  { name: "" },
  { name: "Email" },
  { name: "" },
  { name: "" },
  { name: " " },
  { name: "" },
];

export const getRows = (items: IPartner[]): ITableRow[] => {
  return items.map(({ id, name, email, commission, balance, createDate }: IPartner | any) => ({
    columns: [
      {
        status: "success",
      },
      {
        content: id,
      },
      {
        searchBy: name,
        content: name,
      },
      {
        content: email,
      },
      {
        content: commission || "2%",
      },
      {
        content: balance,
      },
      {
        content: new Date(createDate).toLocaleDateString(),
      },
      {
        content: "   ",
      },
    ],
  }));
};
