import { FC, ReactElement, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import { Button, Paper } from "@material-ui/core";
import { Add as AddIcon } from "@material-ui/icons";
import Table from "components/UI/TableWrapper";
import { NewDialog } from "./NewDialog";

import { getHeaders, getRows } from "./utilsTable";
import { selectPartners } from "store/ducks/owner/selectors";
import { fetchPartners } from "store/ducks/owner/actionCreators";

export const PartnersTable: FC = (): ReactElement => {
  const dispatch = useDispatch();
  const history = useHistory();
  const partners = useSelector(selectPartners);

  const headers = getHeaders();
  const rows = getRows(partners);

  const [openNew, setOpenNew] = useState<boolean>(false);

  const handleNewOpen = () => setOpenNew(true);
  const handleNewClose = () => setOpenNew(false);

  const onRowClick = (e: any, id: number) => history.push(`/partners/${id}`);

  useEffect(() => {
    dispatch(fetchPartners());
  }, [dispatch]);

  return (
    <Paper>
      <Table
        headers={headers}
        rows={rows}
        title=" "
        button={
          <Button variant="outlined" color="primary" startIcon={<AddIcon />} onClick={handleNewOpen}>
            
          </Button>
        }
        onRowClick={onRowClick}
        searchBy="name"
      />

      <NewDialog open={openNew} handleClose={handleNewClose} />
    </Paper>
  );
};
