export enum Status {
  LOADED = "LOADED",
  LOADING = "LOADING",
  ERROR = "ERROR",
  NEVER = "NEVER",
}

export interface IPartner {
  id: number;
  name: string;
  email: string;
  password: string;
  type: number;
  balance: number;
  createDate: number;
  ip: string;
  removed: boolean;
}

export interface ICard {
  id: number;
  monthTransactions: number;
  dayTransactions: number;
  cardNumber: string;
  mm: string;
  yy: string;
  daySum: number;
  monthSum: number;
}

export interface IOwner {
  status: Status;
  partners: IPartner[];
  cards: ICard[];
}
