import produce, { Draft } from "immer";
import { OwnerActions, OwnerActionsTypes } from "./actionCreators";
import { IOwner, Status } from "./state";

const initManage: IOwner = {
  status: Status["NEVER"],
  partners: [],
  cards: [],
};

const ownerReducer = produce((draft: Draft<IOwner>, action: OwnerActions) => {
  switch (action.type) {
    case OwnerActionsTypes.SET_PARTNERS:
      draft.partners = action.payload;
      break;

    case OwnerActionsTypes.ADD_PARTNER:
      draft.status = Status.LOADING;
      break;

    case OwnerActionsTypes.DELETE_PARTNER:
      const partner = draft.partners.findIndex(partner => partner.id === action.payload);
      partner && draft.partners.splice(partner, 1);
      break;

    case OwnerActionsTypes.SET_CARDS:
      draft.cards = action.payload;
      break;

    case OwnerActionsTypes.ADD_CARD:
      draft.status = Status.LOADING;
      break;

    case OwnerActionsTypes.DELETE_CARD:
      const card = draft.cards.findIndex(card => card.id === action.payload);
      card && draft.cards.splice(card, 1);
      break;

    default:
      break;
  }
}, initManage);

export default ownerReducer;
