import { FC, ReactElement, useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useForm } from "react-hook-form";

import Input from "components/UI/Fields/Input";
import Dialog from "components/UI/Dialog";

import { getAccountType } from "utils/getAccountType";
import { IManage } from "store/ducks/manage/state";
import { changeNameWithEmail, changePassword } from "store/ducks/manage/actionCreators";

interface Props {
  open: boolean;
  handleClose: () => void;
  user: IManage["user"];
}

interface IDefaultValues {
  name: string;
  email: string;
  oldPassword: string;
  newPassword: string;
}

export const SettingsDialog: FC<Props> = ({ open, handleClose, user }): ReactElement => {
  const dispatch = useDispatch();

  const defaultValues: IDefaultValues = {
    name: user.name,
    email: user.email,
    oldPassword: "",
    newPassword: "",
  };
  const { control, handleSubmit, watch, reset } = useForm<IDefaultValues>({
    defaultValues,
  });

  const [errors, setErrors] = useState<any>({});

  const name = watch("name");
  const email = watch("email");
  const oldPassword = watch("oldPassword");
  const newPassword = watch("newPassword");

  const onSubmit = () => {
    setErrors({});
    //      

    if (oldPassword) {
      //     8   50 
      if (oldPassword.length < 8 || oldPassword.length > 50) {
        setErrors((prev: any) => ({
          ...prev,
          password: "   8 ,  50",
        }));
        return;
      }

      //    
      if (oldPassword !== newPassword) {
        setErrors((prev: any) => ({
          ...prev,
          password: "  ",
        }));
        return;
      }

      dispatch(changePassword({ oldPassword, newPassword }));
      reset({
        oldPassword: "",
        newPassword: "",
      });
    }

    //   
    if (name !== defaultValues.name || email !== defaultValues.email) {
      //     3      
      if (name.length < 3 || /\d/.test(name.trim())) {
        setErrors((prev: any) => ({
          ...prev,
          name: "          3  ",
        }));
      }

      //        email@example.com
      if (!email || !/\S+@\S+\.\S+/.test(email)) {
        setErrors((prev: any) => ({
          ...prev,
          email: "  ",
        }));
        return;
      }

      const action = dispatch(changeNameWithEmail({ name, email }));
      // ,       
      reset({
        name: action.payload.name,
        email: action.payload.email,
      });
    }

    setErrors({});
    handleClose();
  };

  //       
  useEffect(() => {
    reset(user);
  }, [reset, user]);

  return (
    <Dialog
      handleClose={handleClose}
      open={open}
      title={`  ${getAccountType(user.type)}`}
      form="accountForm">
      <form id="accountForm" onSubmit={handleSubmit(onSubmit)}>
        <Input
          control={control}
          name="name"
          label=""
          type="text"
          defaultValue={user.name}
          error={!!errors.name}
          helperText={errors?.name}
          autoFocus
        />
        <Input
          control={control}
          name="email"
          label="Email"
          type="text"
          defaultValue={user.email}
          error={!!errors.email}
          helperText={errors?.email}
        />
        <Input
          control={control}
          name="oldPassword"
          label=" "
          type="password"
          error={!!errors.password}
          helperText={errors?.password}
        />
        <Input
          control={control}
          name="newPassword"
          label=" "
          type="password"
          error={!!errors.password}
          helperText={errors?.password}
        />
      </form>
    </Dialog>
  );
};
