import { FC, memo } from "react";
import clsx from "clsx";

import { makeStyles, TableCell, TableRow as MuiTableRow } from "@material-ui/core";
import { Status } from "components/UI/Status";

import { ITableRow } from "./types";

interface Props {
  row: ITableRow;
  id: string | number | undefined;
  onRowClick?: any | undefined;
}

const useStyles = makeStyles({
  cell: {
    display: "flex",
    alignItems: "center",
  },
  pointer: {
    cursor: "pointer",
  },
});

const TableRow: FC<Props> = ({ row, id, onRowClick }) => {
  const s = useStyles();

  return (
    <MuiTableRow onClick={e => onRowClick && onRowClick(e, id)} key={id} hover={Boolean(onRowClick)}>
      {row.columns.map((column, i) => (
        <TableCell
          className={clsx({
            [s.pointer]: Boolean(onRowClick),
          })}
          key={i}>
          {column?.status ? (
            <div className={s.cell}>
              {<Status data={column.status} />}
              {column.content}
            </div>
          ) : (
            column.content
          )}
        </TableCell>
      ))}
    </MuiTableRow>
  );
};

export default memo(TableRow);
