import produce, { Draft } from "immer";
import { AuthActions, AuthActionsTypes } from "./actionCreators";
import { Status, IAuth } from "./state";

const initAuth: IAuth = {
  email: "",
  password: "",
  accessToken: "",
  status: Status.NEVER,
};

const authReducer = produce((draft: Draft<IAuth>, action: AuthActions) => {
  switch (action.type) {
    case AuthActionsTypes.LOGIN:
      draft.status = Status.AUTHORIZING;
      break;

    case AuthActionsTypes.LOGOUT:
      draft.email = initAuth.email;
      draft.password = initAuth.password;
      draft.accessToken = initAuth.accessToken;
      draft.status = Status.UNAUTHORIZED;
      localStorage.removeItem("token");
      break;

    case AuthActionsTypes.SET_TOKEN:
      draft.accessToken = action.payload;
      draft.status = Status.AUTHORIZED;
      break;

    case AuthActionsTypes.SET_STATUS:
      draft.status = action.payload;
      break;

    default:
      break;
  }
}, initAuth);

export default authReducer;
