import { FC, ReactElement, useState } from "react";

import { makeStyles, Container } from "@material-ui/core";

import Navbar from "./Navbar";
import Sidebar from "./Sidebar";

import { IRoute } from "constants/ROUTES";
import { BREAKPOINTS } from "constants/BREAKPOINTS";

interface Props {
  route?: IRoute;
  children?: any;
}

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex",
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: "100vh",
    overflow: "auto",
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
}));

const DefaultLayout: FC = ({ route, children }: Props): ReactElement => {
  const s = useStyles();
  const [open, setOpen] = useState<boolean>(window.innerWidth > BREAKPOINTS.lg);

  const handleDrawerOpen = () => setOpen(true);
  const handleDrawerClose = () => setOpen(false);

  return (
    <div className={s.root}>
      <Navbar open={open} handleDrawerOpen={handleDrawerOpen} route={route} />
      <Sidebar open={open} handleDrawerClose={handleDrawerClose} routeName={route?.name} />

      <main className={s.content}>
        <div className={s.appBarSpacer} />
        <Container maxWidth="lg" className={s.container}>
          {children}
        </Container>
      </main>
    </div>
  );
};

export default DefaultLayout;
