import { FC, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useForm } from "react-hook-form";

import { Button, Grid, FormControlLabel, Switch, Collapse } from "@material-ui/core";
import { Delete } from "@material-ui/icons";
import Input from "components/UI/Fields/Input";

import { editShop, deleteShop } from "store/ducks/manage/actionCreators";
import { selectUser } from "store/ducks/manage/selectors";

interface Props {
  id: number | null;
  shop: any;
  form: string;
  handleClose: () => void;
}

interface IDefaultValues {
  name: string;
  failURL: string;
  successURL: string;
  webhookURL: string;
}

export const EditDialogForm: FC<Props> = ({ id, shop, form, handleClose }) => {
  const dispatch = useDispatch();
  const user = useSelector(selectUser);

  const defaultValues: IDefaultValues = {
    name: "",
    failURL: "",
    successURL: "",
    webhookURL: "",
  };
  const { control, handleSubmit, watch, reset } = useForm<IDefaultValues>({
    defaultValues,
  });

  const [errors, setErrors] = useState<any>({});

  const name = watch("name");
  const failURL = watch("failURL");
  const successURL = watch("successURL");
  const webhookURL = watch("webhookURL");

  const isLink = (field: string, value: string): boolean => {
    if (
      !value.match(
        /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_.~#?&//=]*)?/gi,
      )
    ) {
      setErrors((prev: any) => ({
        ...prev,
        [field]: "  ",
      }));

      return false;
    }

    return true;
  };

  const onSubmit = () => {
    setErrors({});

    if (name.length < 3) {
      setErrors((prev: any) => ({
        ...prev,
        name: "     3  ",
      }));
    }

    isLink("failURL", failURL);
    isLink("successURL", successURL);

    //    ,  
    if (id && isLink("webhookURL", webhookURL)) {
      dispatch(editShop({ id, name, failURL, successURL, webhookURL }));
      setErrors({});
      handleClose();
    }
  };

  const handleDelete = () => {
    if (id && window.confirm(`     ${name}?`)) {
      handleClose();
      dispatch(deleteShop(id));
    }
  };

  const [extraSettings, setExtraSettings] = useState(false);

  const handleToggleExtraSettings = () => setExtraSettings(prev => !prev);

  //      
  useEffect(() => {
    shop &&
      reset({
        name: shop.name,
        failURL: shop.failURL,
        successURL: shop.successURL,
        webhookURL: shop.webhookURL,
      });
  }, [reset, shop]);

  return (
    <form id={form} onSubmit={handleSubmit(onSubmit)}>
      <Input
        control={control}
        name="successURL"
        label=" "
        type="text"
        defaultValue={defaultValues.successURL}
        error={!!errors.successURL}
        helperText={errors?.successURL}
        autoFocus
      />
      <Input
        control={control}
        name="failURL"
        label=" "
        type="text"
        defaultValue={defaultValues.failURL}
        error={!!errors.failURL}
        helperText={errors?.failURL}
        required
      />
      <Input
        control={control}
        name="webhookURL"
        label="  "
        type="text"
        defaultValue={defaultValues.webhookURL}
        error={!!errors.webhookURL}
        helperText={errors?.webhookURL}
      />
      <Input
        control={control}
        name="name"
        label=""
        type="text"
        defaultValue={defaultValues.name}
        error={!!errors.name}
        helperText={errors?.name}
        required
      />

      {user.type === 3 && (
        <>
          <FormControlLabel
            control={<Switch checked={extraSettings} onChange={handleToggleExtraSettings} />}
            label=" "
          />
          <Collapse in={extraSettings}>
            <Grid container alignItems="center" spacing={1}>
              <Grid xs={6} item>
                <Input
                  control={control}
                  name="amountmin"
                  label=" "
                  type="number"
                  // defaultValue={defaultValues.successURL}
                  // error={!!errors.successURL}
                  // helperText={errors?.successURL}
                />
              </Grid>
              <Grid xs={6} item>
                <Input
                  control={control}
                  name="amountmax"
                  label=" "
                  type="number"
                  // defaultValue={defaultValues.webhookURL}
                  // error={!!errors.webhookURL}
                  // helperText={errors?.webhookURL}
                />
              </Grid>

              <Grid xs={12} item>
                <Input
                  control={control}
                  name="amountday"
                  label="  "
                  type="number"
                  // defaultValue={defaultValues.successURL}
                  // error={!!errors.successURL}
                  // helperText={errors?.successURL}
                />
              </Grid>
            </Grid>
          </Collapse>
        </>
      )}
      <Button
        variant="outlined"
        color="secondary"
        startIcon={<Delete />}
        onClick={handleDelete}
        style={{ marginTop: "1rem" }}
        fullWidth>
         
      </Button>
    </form>
  );
};
