import { FC, useState } from "react";
import { useDispatch } from "react-redux";
import { useForm } from "react-hook-form";

import Dialog from "components/UI/Dialog";
import Input from "components/UI/Fields/Input";

import { addPartner } from "store/ducks/owner/actionCreators";

interface Props {
  open: boolean;
  handleClose: () => void;
}

interface IDefaultValues {
  email: string;
  name: string;
  password: string;
}

export const NewDialog: FC<Props> = ({ open, handleClose }) => {
  const dispatch = useDispatch();

  const defaultValues: IDefaultValues = {
    email: "",
    name: "",
    password: "",
  };
  const { control, handleSubmit, watch } = useForm<IDefaultValues>({
    defaultValues,
  });

  const [errors, setErrors] = useState<any>({});

  const email = watch("email");
  const name = watch("name");
  const password = watch("password");

  const onSubmit = () => {
    setErrors({});

    //        email@example.com
    if (!email || !/\S+@\S+\.\S+/.test(email)) {
      setErrors((prev: any) => ({
        ...prev,
        email: "  ",
      }));
      return;
    }

    if (name.length < 3) {
      setErrors((prev: any) => ({
        ...prev,
        name: "     3  ",
      }));
    }

    if (!password) {
      setErrors((prev: any) => ({
        ...prev,
        password: "  ",
      }));
      return;
    }

    if (password.length < 8 || password.length > 50) {
      setErrors((prev: any) => ({
        ...prev,
        password: "   8 ,  50",
      }));
      return;
    }

    dispatch(addPartner({ email, name, password }));
    setErrors({});
    handleClose();
  };

  return (
    <Dialog open={open} handleClose={handleClose} title=" " form="addPartner">
      <form id="addPartner" onSubmit={handleSubmit(onSubmit)}>
        <Input
          control={control}
          name="email"
          label="Email"
          type="text"
          defaultValue={defaultValues.email}
          error={!!errors.email}
          helperText={errors?.email}
          required
          autoFocus
        />
        <Input
          control={control}
          name="name"
          label=""
          type="text"
          defaultValue={defaultValues.name}
          error={!!errors.name}
          helperText={errors?.name}
          required
        />
        <Input
          control={control}
          name="password"
          label=""
          type="password"
          defaultValue={defaultValues.password}
          error={!!errors.password}
          helperText={errors?.password}
          required
        />
      </form>
    </Dialog>
  );
};
