import { FC, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import { List, ListItem, Button, Menu, MenuItem } from "@material-ui/core";
import { Refresh, Equalizer } from "@material-ui/icons";
import Dialog from "components/UI/Dialog";
import { Status } from "components/UI/Status";
import { EditDialogForm } from "./EditDialogForm";

import { getPrice } from "utils/getPrice";

import { RootState } from "store/rootReducer";
import { refreshKey } from "store/ducks/manage/actionCreators";
import { selectShop } from "store/ducks/manage/selectors";

interface Props {
  id: number | null;
  handleClose: () => void;
}

export const EditDialog: FC<Props> = ({ id, handleClose }) => {
  const dispatch = useDispatch();
  const shop = useSelector((state: RootState) => id && (selectShop(state, id) as any));

  const [stats, setStats] = useState<null | HTMLElement>(null);
  const [period, setPeriod] = useState(" ");

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => setStats(event.currentTarget);
  const handlePickStats = (e: any) => {
    e.currentTarget.innerText && setPeriod(e.currentTarget.innerText);
    setStats(null);
  };

  const refreshPublicKey = () => id && dispatch(refreshKey({ id, value: "publicKey" }));
  const refreshSecretKey = () => id && dispatch(refreshKey({ id, value: "webhookSecretKey" }));

  return (
    <Dialog
      open={Boolean(id)}
      handleClose={handleClose}
      title={
        <>
            (id: {id})
          <Status data={true ? "success" : shop?.status} />
        </>
      }
      form="editSite">
      <List dense>
        <Button startIcon={<Refresh />} title="" onClick={refreshPublicKey}>
           
        </Button>
        {shop && shop.publicKey}
        <Button startIcon={<Refresh />} title="" onClick={refreshSecretKey}>
           
        </Button>
        {shop && shop.webhookSecretKey}
        <Button aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick} startIcon={<Equalizer />}>
           {period}
        </Button>
        <Menu anchorEl={stats} keepMounted open={Boolean(stats)} onClose={handlePickStats}>
          <MenuItem onClick={handlePickStats}> </MenuItem>
          <MenuItem onClick={handlePickStats}> </MenuItem>
          {/* <MenuItem onClick={handlePickStats}> </MenuItem> */}
        </Menu>
        <ListItem>  : 3000</ListItem>
        <ListItem>   : 1000</ListItem>
        <ListItem> : {getPrice(29400)}</ListItem>
      </List>

      <EditDialogForm id={id} shop={shop} form="editSite" handleClose={handleClose} />
    </Dialog>
  );
};
