import { RootState } from "store/rootReducer";
import { IManage, IShop } from "./state";

export const selectUser = (state: RootState): IManage["user"] => state.manage.user;
export const selectShops = (state: RootState): IShop[] => state.manage.user.shops;
export const selectShop = (state: RootState, id: number): IShop | undefined =>
  state.manage.user.shops.find(shop => shop.id === id);
