import { FC, ReactElement, memo, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import clsx from "clsx";

import { makeStyles, AppBar, Toolbar, Typography, IconButton, Menu, MenuItem, Button } from "@material-ui/core";
import { Menu as MenuIcon, AccountCircle, ExitToApp } from "@material-ui/icons";
import { SettingsDialog } from "./SettingsDialog";

import { BREAKPOINTS } from "constants/BREAKPOINTS";
import { IRoute } from "constants/ROUTES";
import { isAuthorized } from "utils/isAuthorized";

import { Status } from "store/ducks/auth/state";
import { logout, setAuthStatus } from "store/ducks/auth/actionCreators";
import { selectUser } from "store/ducks/manage/selectors";
import { fetchUser } from "store/ducks/manage/actionCreators";

interface Props {
  open: boolean;
  handleDrawerOpen: () => void;
  route?: IRoute;
}

const drawerWidth = 240;
const useStyles = makeStyles(theme => ({
  toolbar: {
    paddingRight: 24,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: "none",
  },
  title: {
    flexGrow: 1,
  },
  login: {
    marginLeft: "auto",
  },
}));

const Navbar: FC<Props> = ({ open, handleDrawerOpen, route }): ReactElement => {
  const s = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();
  const user = useSelector(selectUser);

  const [dialogOpen, setDialogOpen] = useState(false);
  const [menu, setMenu] = useState<null | HTMLElement>(null);

  const handleMenuOpen = (event: React.MouseEvent<HTMLButtonElement>) => setMenu(event.currentTarget);
  const handleMenuClose = () => setMenu(null);

  const handleDialogOpen = () => {
    handleMenuClose();
    setDialogOpen(true);
  };
  const handleDialogClose = () => {
    handleMenuClose();
    setDialogOpen(false);
  };

  const handleLogout = () => {
    handleMenuClose();
    dispatch(logout());
    history.push("/auth");
  };

  useEffect(() => {
    dispatch(fetchUser());
  }, [dispatch]);

  useEffect(() => {
    isAuthorized() && dispatch(setAuthStatus(Status["AUTHORIZED"]));
  });

  //         
  useEffect(() => {
    if (route?.access) {
      route.access.findIndex(role => role === user.type) < 0 && history.push("/404");
    }
  }, [route, history, user.type]);

  return (
    <AppBar position="absolute" className={clsx(s.appBar, open && s.appBarShift)}>
      <Toolbar className={s.toolbar}>
        <IconButton
          edge="start"
          color="inherit"
          onClick={handleDrawerOpen}
          className={clsx(s.menuButton, open && s.menuButtonHidden)}>
          <MenuIcon />
        </IconButton>
        <Typography variant="h6" color="inherit" noWrap className={s.title}>
          {window.innerWidth > BREAKPOINTS.lg ? route?.name : !open ? route?.name : ""}
        </Typography>
        {user.email ? (
          <Button onClick={handleMenuOpen} color="inherit">
            {user.email}
          </Button>
        ) : (
          <IconButton edge="start" color="inherit" onClick={handleLogout}>
            <ExitToApp />
          </IconButton>
        )}
        <Menu id="simple-menu" anchorEl={menu} keepMounted open={Boolean(menu)} onClose={handleMenuClose}>
          <MenuItem onClick={handleDialogOpen}>
            <AccountCircle />  
          </MenuItem>
          <MenuItem onClick={handleLogout}>
            <ExitToApp />  
          </MenuItem>
        </Menu>
      </Toolbar>

      <SettingsDialog open={dialogOpen} handleClose={handleDialogClose} user={user} />
    </AppBar>
  );
};

export default memo(Navbar);
