import { RootState } from "store/rootReducer";
import { ICard, IPartner } from "./state";

export const selectPartners = (state: RootState): IPartner[] => state.owner.partners;
export const selectPartner = (state: RootState, id: number): IPartner | undefined =>
  state.owner.partners.find(partner => partner.id === id);

export const selectCards = (state: RootState): ICard[] => state.owner.cards;
export const selectCard = (state: RootState, id: number): ICard | undefined =>
  state.owner.cards.find(card => card.id === id);
