import { FC, useState } from "react";
import { useDispatch } from "react-redux";
import { useForm } from "react-hook-form";

import { Grid } from "@material-ui/core";

import Dialog from "components/UI/Dialog";
import Input from "components/UI/Fields/Input";

import { addCard } from "store/ducks/owner/actionCreators";

interface Props {
  open: boolean;
  handleClose: () => void;
}

interface IDefaultValues {
  cardNumber: string;
  mm: string;
  yy: string;
  cvv: string;
}

export const NewDialog: FC<Props> = ({ open, handleClose }) => {
  const dispatch = useDispatch();

  const defaultValues: IDefaultValues = {
    cardNumber: "",
    mm: "",
    yy: "",
    cvv: "",
  };
  const { control, handleSubmit, watch } = useForm<IDefaultValues>({
    defaultValues,
  });

  const [errors, setErrors] = useState<any>({});

  const cardNumber = watch("cardNumber");
  const mm = watch("mm");
  const yy = watch("yy");
  const cvv = watch("cvv");

  const onSubmit = () => {
    setErrors({});

    if (cardNumber.length < 16) {
      setErrors((prev: any) => ({
        ...prev,
        cardNumber: "     16 ",
      }));
    }

    if (mm.length < 2) {
      setErrors((prev: any) => ({
        ...prev,
        mm: "      2 ",
      }));
    }
    if (yy.length < 2) {
      setErrors((prev: any) => ({
        ...prev,
        yy: "      2 ",
      }));
    }
    if (cvv.length < 3) {
      setErrors((prev: any) => ({
        ...prev,
        cvv: "CVV     3 ",
      }));
      return;
    }

    dispatch(addCard({ cardNumber, mm, yy, cvv }));
    setErrors({});
    handleClose();
  };

  return (
    <Dialog open={open} handleClose={handleClose} title=" " form="addCard">
      <form id="addCard" onSubmit={handleSubmit(onSubmit)}>
        <Input
          control={control}
          name="cardNumber"
          label=""
          type="text"
          defaultValue={defaultValues.cardNumber}
          error={!!errors.cardNumber}
          helperText={errors?.cardNumber}
          maxLength={16}
          required
          autoFocus
        />
        <Grid container spacing={1}>
          <Grid xs={4} item>
            <Input
              control={control}
              name="mm"
              label=""
              type="text"
              defaultValue={defaultValues.mm}
              error={!!errors.mm}
              helperText={errors?.mm}
              maxLength={2}
              required
            />
          </Grid>
          <Grid xs={4} item>
            <Input
              control={control}
              name="yy"
              label=""
              type="text"
              defaultValue={defaultValues.yy}
              error={!!errors.yy}
              helperText={errors?.yy}
              maxLength={2}
              required
            />
          </Grid>
          <Grid xs={4} item>
            <Input
              control={control}
              name="cvv"
              label="CVV"
              type="text"
              defaultValue={defaultValues.cvv}
              error={!!errors.cvv}
              helperText={errors?.cvv}
              maxLength={3}
              required
            />
          </Grid>
        </Grid>
        <Input
          control={control}
          name="comment"
          label=""
          type="text"
          // defaultValue={defaultValues.comment}
          // error={!!errors.comment}
          // helperText={errors?.comment}
        />
      </form>
    </Dialog>
  );
};
