import { Action } from "redux";
import { IShop, IUser } from "./state";

export enum ManageActionsTypes {
  CHANGE_NAME_EMAIL = "manage/CHANGE_NAME_EMAIL",
  CHANGE_PASSWORD = "manage/CHANGE_PASSWORD",

  SET_USER = "manage/SET_USER",
  FETCH_USER = "manage/FETCH_USER",

  SET_SHOPS = "manage/SET_SHOPS",
  FETCH_SHOPS = "manage/FETCH_SHOPS",
  ADD_SHOP = "manage/ADD_SHOP",
  EDIT_SHOP = "manage/EDIT_SHOP",
  DELETE_SHOP = "manage/DELETE_SHOP",

  KEY_REFRESH = "manage/KEY_REFRESH",
  SET_KEY = "manage/SET_KEY",
}

// ACTIONS TYPES
export interface IChangeNameWithEmail extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.CHANGE_NAME_EMAIL;
  payload: { name: string; email: string };
}
export interface IChangePassword extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.CHANGE_PASSWORD;
  payload: { oldPassword: string; newPassword: string };
}

export interface IFetchUser extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.FETCH_USER;
}
export interface ISetUserAction extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.SET_USER;
  payload: IUser;
}

export interface IFetchShops extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.FETCH_SHOPS;
}
export interface ISetShops extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.SET_SHOPS;
  payload: IShop[];
}
export interface IAddShop extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.ADD_SHOP;
  payload: { name: string; siteURL: string };
}

export interface IEditShop extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.EDIT_SHOP;
  payload: { id: number; name: string; failURL: string; successURL: string; webhookURL: string };
}
export interface IDeleteShop extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.DELETE_SHOP;
  payload: IShop["id"];
}

export interface IRefreshKey extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.KEY_REFRESH;
  payload: { id: IShop["id"]; value: "publicKey" | "webhookSecretKey" };
}
export interface ISetKey extends Action<ManageActionsTypes> {
  type: ManageActionsTypes.SET_KEY;
  payload: { id: IShop["id"]; [key: string]: any };
}

// ACTIONS
export const changeNameWithEmail = (payload: IChangeNameWithEmail["payload"]): IChangeNameWithEmail => ({
  type: ManageActionsTypes.CHANGE_NAME_EMAIL,
  payload,
});
export const changePassword = (payload: IChangePassword["payload"]): IChangePassword => ({
  type: ManageActionsTypes.CHANGE_PASSWORD,
  payload,
});

export const fetchUser = (): IFetchUser => ({
  type: ManageActionsTypes.FETCH_USER,
});
export const setUser = (payload: IUser): ISetUserAction => ({
  type: ManageActionsTypes.SET_USER,
  payload,
});

export const fetchShops = (): IFetchShops => ({
  type: ManageActionsTypes.FETCH_SHOPS,
});
export const setShops = (payload: IShop[]): ISetShops => ({
  type: ManageActionsTypes.SET_SHOPS,
  payload,
});
export const addShop = (payload: IAddShop["payload"]): IAddShop => ({
  type: ManageActionsTypes.ADD_SHOP,
  payload,
});
export const editShop = (payload: IEditShop["payload"]): IEditShop => ({
  type: ManageActionsTypes.EDIT_SHOP,
  payload,
});
export const deleteShop = (payload: IDeleteShop["payload"]): IDeleteShop => ({
  type: ManageActionsTypes.DELETE_SHOP,
  payload,
});

export const refreshKey = (payload: IRefreshKey["payload"]): IRefreshKey => ({
  type: ManageActionsTypes.KEY_REFRESH,
  payload,
});
export const setKey = (payload: ISetKey["payload"]): ISetKey => ({
  type: ManageActionsTypes.SET_KEY,
  payload,
});

export type ManageActions =
  | IChangeNameWithEmail
  | IChangePassword
  | IFetchUser
  | ISetUserAction
  | IFetchShops
  | ISetShops
  | IAddShop
  | IEditShop
  | IDeleteShop
  | IRefreshKey
  | ISetKey;
