import { FC, ReactElement, useState } from "react";
// import { useDispatch, useSelector } from "react-redux";

import { Paper } from "@material-ui/core";
import Table from "components/UI/TableWrapper";
import { EditDialog } from "./EditDialog";

import { getHeaders, getRows } from "./utilsTable";
// import { selectPartners } from "store/ducks/owner/selectors";
// import { fetchPartners } from "store/ducks/owner/actionCreators";

interface Props {
  type: string;
}

export const PaymentsTable: FC<Props> = ({ type }): ReactElement => {
  // const dispatch = useDispatch();
  // const payments = useSelector();
  const payments =
    type === "regular"
      ? [
          {
            id: 1,
            name: "test",
            amount: 1000,
            createDate: Date.now(),
            ip: " ",
          },
        ]
      : [
          {
            id: 1,
            name: "test",
            amount: 1000,
            createDate: Date.now(),
            ip: " ",
          },
        ];

  const headers = getHeaders();
  const rows = getRows(payments);

  const [editID, setEditID] = useState<number | null>(null);

  const onRowClick = (e: any, id: number) => setEditID(id);
  const handleEditClose = () => setEditID(null);

  // useEffect(() => {
  //   dispatch(fetchPartners());
  // }, [dispatch]);

  return (
    <Paper>
      <Table
        headers={headers}
        rows={rows}
        title={type === "regular" ? " " : " "}
        onRowClick={onRowClick}
      />

      <EditDialog id={editID} handleClose={handleEditClose} />
    </Paper>
  );
};
