import produce, { Draft } from "immer";
import { ManageActions, ManageActionsTypes } from "./actionCreators";
import { IManage, IShop, Status } from "./state";

const initManage: IManage = {
  status: Status["NEVER"],
  user: {
    id: -1,
    name: "",
    email: "",
    type: 0,
    balance: 0,
    shops: [],
  },
};

const manageReducer = produce((draft: Draft<IManage>, action: ManageActions) => {
  switch (action.type) {
    case ManageActionsTypes.CHANGE_NAME_EMAIL:
      draft.user = {
        ...draft.user,
        name: action.payload.name,
        email: action.payload.email,
      };
      break;

    case ManageActionsTypes.SET_USER:
      draft.user = action.payload;
      break;

    case ManageActionsTypes.SET_SHOPS:
      draft.user.shops = action.payload;
      break;
    case ManageActionsTypes.ADD_SHOP:
      draft.status = Status.LOADING;
      break;
    case ManageActionsTypes.DELETE_SHOP:
      const index = draft.user.shops.findIndex(shop => shop.id === action.payload);
      index && draft.user.shops.splice(index, 1);
      break;

    case ManageActionsTypes.SET_KEY:
      let shop: IShop | any = draft.user.shops[draft.user.shops.findIndex(shop => shop.id === action.payload.id)];

      if (shop) {
        const key = Object.keys(action.payload)[1];
        shop[key] = action.payload[key];
      }
      break;

    default:
      break;
  }
}, initManage);

export default manageReducer;
