import { FC, memo } from "react";
import { Controller } from "react-hook-form";
import { Control } from "react-hook-form/dist/types/form";
import { TextField } from "@material-ui/core";

interface Props {
  control: Control<any>;
  name: string;
  defaultValue?: string | number;
  type?: string;
  label?: string;
  error?: any;
  variant?: string;
  margin?: string;
  helperText?: any;
  required?: boolean;
  fullWidth?: boolean;
  maxLength?: number;
  autoComplete?: string;
  autoFocus?: boolean;
}

const Input: FC<Props> = ({
  control,
  name,
  defaultValue = "",
  type = "text",
  label,
  error,
  helperText,
  required,
  fullWidth = true,
  maxLength,
  autoComplete,
  autoFocus,
}) => {
  return (
    <Controller
      name={name}
      control={control}
      defaultValue={defaultValue}
      render={({ field }) => (
        <TextField
          label={label}
          type={type}
          variant="outlined"
          margin="normal"
          error={error}
          required={required}
          helperText={helperText}
          autoComplete={autoComplete}
          autoFocus={autoFocus}
          fullWidth={fullWidth}
          inputProps={{
            maxLength,
          }}
          {...field}
          //  warning  
          value={field.value || ""}
        />
      )}
    />
  );
};

export default memo(Input);
