import { FC, memo, ReactElement, ChangeEvent, useState, useEffect } from "react";
import { makeStyles, TextField } from "@material-ui/core";

import { ITableHeader } from "./types";
import { ITableRow } from "./types";
import {
  Paper,
  TableContainer,
  Table,
  TableBody,
  TableRow as MuiTableRow,
  TableHead,
  TableCell,
  TablePagination,
  Typography,
} from "@material-ui/core";
import TableRow from "./Row";

interface Props {
  title?: any;
  button?: ReactElement;
  rows: ITableRow[];
  headers: ITableHeader[];
  searchBy?: string;
  onRowClick?: (e: any, id: number) => void | undefined;
}

const useStyles = makeStyles(theme => ({
  container: {
    overflowX: "auto",
    boxShadow: "none",
  },
  tableHeader: {
    display: "flex",
    alignItems: "center",
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
  },
  title: {
    marginRight: "auto",
  },
  search: {
    marginRight: theme.spacing(1),
  },
}));

const getSearchLabel = (field: Props["searchBy"]) => {
  switch (field) {
    case "name":
      return "";
    default:
      break;
  }
};

const TableWrapper: FC<Props> = ({ title, button, headers, rows, searchBy, onRowClick }) => {
  const s = useStyles();

  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [page, setPage] = useState(0);
  const [searchText, setSearchText] = useState("");
  const [rowsCopy, setRowsCopy] = useState<ITableRow[]>([]);

  const handleChangePage = (e: any, newPage: number) => setPage(newPage);
  const handleChangeRowsPerPage = (e: ChangeEvent<HTMLInputElement>) => {
    setRowsPerPage(parseInt(e.target.value, 10));
    setPage(0);
  };

  const handleSearch = (e: any) => setSearchText(e.target.value.toLowerCase());

  useEffect(() => {
    if (searchText.length) {
      const results = rows.filter(row =>
        row.columns.find(col => col.searchBy && col.searchBy.toLowerCase().includes(searchText)),
      );
      setRowsCopy(results);
    } else {
      setRowsCopy(rows);
    }
  }, [searchText, rows]);

  return (
    <>
      {(title || button) && (
        <div className={s.tableHeader}>
          <Typography variant="h6" className={s.title}>
            {title}
          </Typography>
          {searchBy && (
            <TextField
              className={s.search}
              variant="outlined"
              size="small"
              name="searchBy"
              label={`  ${getSearchLabel(searchBy)}`}
              onChange={handleSearch}
            />
          )}
          {button}
        </div>
      )}
      <Paper className={s.container}>
        <TableContainer>
          <Table>
            <TableHead>
              <MuiTableRow>
                {headers.map((column, i) => (
                  <TableCell key={i}>{column.name}</TableCell>
                ))}
              </MuiTableRow>
            </TableHead>

            <TableBody>
              {rowsCopy && rowsCopy.length > 0 ? (
                rowsCopy
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((row, i) => (
                    <TableRow
                      row={row}
                      id={row.columns.find(col => typeof col.content === "number")?.content}
                      onRowClick={onRowClick}
                      key={i}
                    />
                  ))
              ) : rows?.length ? (
                <MuiTableRow>
                  <TableCell colSpan={100}>  </TableCell>
                </MuiTableRow>
              ) : (
                <MuiTableRow>
                  <TableCell colSpan={100}>   </TableCell>
                </MuiTableRow>
              )}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      </Paper>
    </>
  );
};

export default memo(TableWrapper);
