import { FC, useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useForm } from "react-hook-form";

import { Button, Grid, Menu, MenuItem, List, ListItem } from "@material-ui/core";
import { Delete, Equalizer } from "@material-ui/icons";
import Dialog from "components/UI/Dialog";
import Input from "components/UI/Fields/Input";

import { RootState } from "store/rootReducer";
import { deleteCard } from "store/ducks/owner/actionCreators";
import { selectCard } from "store/ducks/owner/selectors";

interface Props {
  id: number | null;
  handleClose: () => void;
}

interface IDefaultValues {
  cardNumber: string;
  comment: string;
}

export const EditDialog: FC<Props> = ({ id, handleClose }) => {
  const dispatch = useDispatch();
  const card = useSelector((state: RootState) => id && (selectCard(state, id) as any));

  const defaultValues: IDefaultValues = {
    cardNumber: card?.cardNumber,
    comment: "",
  };
  const { control, watch, handleSubmit, reset } = useForm<IDefaultValues>({
    defaultValues,
  });

  const [errors, setErrors] = useState<any>({});

  const cardNumber = watch("cardNumber");
  // const comment = watch("comment");

  const onSubmit = () => {
    setErrors({});

    if (cardNumber.length < 16) {
      setErrors((prev: any) => ({
        ...prev,
        cardNumber: "     16 ",
      }));
      return;
    }

    // dispatch(editCard({ cardNumber, mm, yy }));
    setErrors({});
    handleClose();
  };

  const [stats, setStats] = useState<null | HTMLElement>(null);
  const [period, setPeriod] = useState(" ");

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => setStats(event.currentTarget);
  const handlePickStats = (e: any) => {
    e.currentTarget.innerText && setPeriod(e.currentTarget.innerText);
    setStats(null);
  };

  const handleDelete = () => {
    if (id && window.confirm(`    ?`)) {
      handleClose();
      dispatch(deleteCard(id));
    }
  };

  //      
  useEffect(() => {
    card &&
      reset({
        cardNumber: card?.cardNumber,
        // comment: card.comment
      });
  }, [reset, card]);

  return (
    <Dialog
      open={Boolean(id)}
      handleClose={handleClose}
      title={`${card?.cardNumber} (id: ${card?.id})`}
      form="editCard">
      <Button onClick={handleClick} startIcon={<Equalizer />}>
         {period}
      </Button>
      <Grid item xs={12}>
        <List dense>
          <ListItem>: {period === " " ? card?.dayTransactions : card?.monthTransactions}</ListItem>
          <ListItem>: {period === " " ? card?.daySum : card?.monthSum}</ListItem>
        </List>
      </Grid>
      <Menu anchorEl={stats} keepMounted open={Boolean(stats)} onClose={handlePickStats}>
        <MenuItem onClick={handlePickStats}> </MenuItem>
        <MenuItem onClick={handlePickStats}> </MenuItem>
        {/* <MenuItem onClick={handlePickStats}> </MenuItem> */}
      </Menu>
      <form id="editCard" onSubmit={handleSubmit(onSubmit)}>
        <Input
          control={control}
          name="number"
          label=""
          type="text"
          defaultValue={defaultValues.cardNumber}
          error={!!errors.cardNumber}
          helperText={errors?.cardNumber}
          required
          autoFocus
        />
        <Input
          control={control}
          name="comment"
          label=""
          type="text"
          defaultValue={defaultValues.comment}
          error={!!errors.comment}
          helperText={errors?.comment}
        />
      </form>
      <Button
        variant="outlined"
        color="secondary"
        startIcon={<Delete />}
        onClick={handleDelete}
        style={{ marginTop: "1rem" }}>
         
      </Button>
    </Dialog>
  );
};
