import axios from "plugins/axios";
import { IUser } from "store/ducks/manage/state";
import { IAddCard, IAddPartner, IDeleteCard, IDeletePartner } from "store/ducks/owner/actionCreators";
import { IPartner, ICard } from "store/ducks/owner/state";
import { API_URL } from "./constants";

export const API_OWNER = {
  async fetchPartners(): Promise<IPartner[]> {
    const response = await axios.get(`${API_URL}/owner/getPartnersList`);
    return response.data.list;
  },
  async addPartner({ email, name, password }: IAddPartner["payload"]): Promise<IUser> {
    const response = await axios.post(`${API_URL}/owner/register`, { email, name, password });
    return response.data.user;
  },
  async deletePartner(id: IDeletePartner["payload"]): Promise<{ success: boolean }> {
    const response = await axios.post(`${API_URL}/owner/removePartner`, { id });
    return response.data.success;
  },

  async fetchCards(): Promise<ICard[]> {
    const response = await axios.get(`${API_URL}/owner/getTargetsList`);
    return response.data.list;
  },
  // async fetchCard(id: ICard["id"]): Promise<ICard> {
  //   const response = await axios.get(`${API_URL}/owner/getTargetById?id=${id}`);
  //   return response.data.target;
  // },
  async addCard({ cardNumber, mm, yy }: IAddCard["payload"]): Promise<IUser> {
    const response = await axios.post(`${API_URL}/owner/addTarget`, { cardNumber, mm, yy });
    return response.data.target;
  },
  async deleteCard(id: IDeleteCard["payload"]): Promise<{ success: boolean }> {
    const response = await axios.post(`${API_URL}/owner/removeTarget`, { id });
    return response.data.success;
  },
};
