export enum Status {
  LOADED = "LOADED",
  LOADING = "LOADING",
  ERROR = "ERROR",
  NEVER = "NEVER",
}

export interface IShop {
  id: number;
  name: string;
  siteURL: string;
  successURL: string;
  failURL: string;
  webhookURL: string;
  createDate: number;
  publicKey?: string;
  webhookSecretKey?: string;
}

export interface IUser {
  id: number;
  name: string;
  email: string;
  type: number;
  balance: number;
  shops: IShop[];
}
export interface IManage {
  status: Status;
  user: IUser;
}
