import { ITableHeader, ITableRow } from "components/UI/TableWrapper/types";
import { ICard } from "store/ducks/owner/state";

function parseExpirationDate(mm: number, yy: number): string {
  return mm < 10 ? `0${mm}/${yy}` : `${mm}/${yy}`;
}

export const getHeaders = (): ITableHeader[] => [
  { name: "id" },
  { name: "" },
  { name: "" },
  { name: "" },
  { name: "" },
  { name: "" },
];

export const getRows = (items: ICard[] | any): ITableRow[] => {
  return items.map(({ id, name, currency, cardNumber, mm, yy, comment }: ICard | any) => ({
    columns: [
      {
        content: id,
      },
      {
        content: name || "VISA",
      },
      {
        content: currency || "RUB",
      },
      {
        content: cardNumber,
      },
      {
        content: parseExpirationDate(mm, yy),
      },
      {
        content: comment || "   ",
      },
    ],
  }));
};
