export enum Status {
  AUTHORIZING = "AUTHORIZING",
  AUTHORIZED = "AUTHORIZED",
  UNAUTHORIZED = "UNAUTHORIZED",
  ERROR = "ERROR",
  NEVER = "NEVER",
}

export interface ILogin {
  email: string;
  password: string;
}

export interface IToken {
  accessToken: string;
  status: Status;
}

export type IAuth = ILogin & IToken;
