import { FC, useState } from "react";
import { useDispatch } from "react-redux";
import { useForm } from "react-hook-form";

import Dialog from "components/UI/Dialog";
import Input from "components/UI/Fields/Input";

import { addShop } from "store/ducks/manage/actionCreators";

interface Props {
  open: boolean;
  handleClose: () => void;
}

interface IDefaultValues {
  name: string;
  siteURL: string;
}

export const NewDialog: FC<Props> = ({ open, handleClose }) => {
  const dispatch = useDispatch();

  const defaultValues: IDefaultValues = {
    name: "",
    siteURL: "",
  };
  const { control, handleSubmit, watch } = useForm<IDefaultValues>({
    defaultValues,
  });

  const [errors, setErrors] = useState<any>({});

  const name = watch("name");
  const siteURL = watch("siteURL");

  const onSubmit = () => {
    setErrors({});

    if (name.length < 3) {
      setErrors((prev: any) => ({
        ...prev,
        name: "     3  ",
      }));
    }

    if (
      !siteURL.match(
        /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_.~#?&//=]*)?/gi,
      )
    ) {
      setErrors((prev: any) => ({
        ...prev,
        siteURL: "  ",
      }));
      return;
    }

    dispatch(addShop({ name, siteURL }));
    setErrors({});
    handleClose();
  };

  return (
    <Dialog open={open} handleClose={handleClose} title=" " form="addShop">
      <form id="addShop" onSubmit={handleSubmit(onSubmit)}>
        <Input
          control={control}
          name="name"
          label=""
          type="text"
          defaultValue={defaultValues.name}
          error={!!errors.name}
          helperText={errors?.name}
          autoFocus
        />
        <Input
          control={control}
          name="siteURL"
          label="  "
          type="text"
          defaultValue={defaultValues.siteURL}
          error={!!errors.siteURL}
          helperText={errors?.siteURL}
        />
      </form>
    </Dialog>
  );
};
