import { combineReducers } from "redux";

import { IAuth } from "./ducks/auth/state";
import { IManage } from "./ducks/manage/state";
import { IOwner } from "./ducks/owner/state";

import authReducer from "./ducks/auth/reducer";
import manageReducer from "./ducks/manage/reducer";
import ownerReducer from "./ducks/owner/reducer";

export interface RootState {
  auth: IAuth;
  manage: IManage;
  owner: IOwner;
}

export const rootReducer = combineReducers<RootState>({
  auth: authReducer,
  manage: manageReducer,
  owner: ownerReducer,
});
