import { Action } from "redux";
import { Status, ILogin } from "./state";

export enum AuthActionsTypes {
  LOGIN = "auth/LOGIN",
  LOGOUT = "auth/LOGOUT",
  SET_STATUS = "auth/SET_STATUS",
  SET_TOKEN = "auth/SET_TOKEN",
}

// ACTIONS TYPES
interface ILoginActionPayload extends ILogin {
  history: any;
}
export interface ILoginAction extends Action<AuthActionsTypes> {
  type: AuthActionsTypes.LOGIN;
  payload: ILoginActionPayload;
}
export interface ILogoutAction extends Action<AuthActionsTypes> {
  type: AuthActionsTypes.LOGOUT;
}

export interface IAuthStatus extends Action<AuthActionsTypes> {
  type: AuthActionsTypes.SET_STATUS;
  payload: Status;
}
export interface ISetAccessToken extends Action<AuthActionsTypes> {
  type: AuthActionsTypes.SET_TOKEN;
  payload: string;
}

// ACTIONS
export const login = (payload: ILoginActionPayload): ILoginAction => ({
  type: AuthActionsTypes.LOGIN,
  payload,
});
export const logout = (): ILogoutAction => ({
  type: AuthActionsTypes.LOGOUT,
});

export const setAuthStatus = (payload: Status): IAuthStatus => ({
  type: AuthActionsTypes.SET_STATUS,
  payload,
});
export const setAccessToken = (payload: string): ISetAccessToken => ({
  type: AuthActionsTypes.SET_TOKEN,
  payload,
});

export type AuthActions = ILoginAction | ILogoutAction | IAuthStatus | ISetAccessToken;
