import { FC, memo } from "react";

import { Dialog as MuiDialog, DialogContent, DialogActions, DialogTitle, IconButton, Button } from "@material-ui/core";
import { Close as CloseIcon } from "@material-ui/icons";

interface Props {
  open: boolean;
  handleClose: () => void;
  title?: any;
  form?: string;
  children: any;
}

const Dialog: FC<Props> = ({ open, handleClose, title, form, children }) => {
  return (
    <MuiDialog onClose={handleClose} open={open}>
      {title && (
        <DialogTitle>
          {title}
          <IconButton onClick={handleClose}>
            <CloseIcon />
          </IconButton>
        </DialogTitle>
      )}
      <DialogContent dividers>{children}</DialogContent>
      <DialogActions>
        <Button onClick={handleClose} color="default">
          
        </Button>
        {form && (
          <Button type="submit" form={form} color="primary">
            
          </Button>
        )}
      </DialogActions>
    </MuiDialog>
  );
};

export default memo(Dialog);
