import { SagaIterator } from "redux-saga";
import { all, call, put, takeEvery } from "redux-saga/effects";

import { errorHandler } from "utils/errorHandler";

import { API_OWNER } from "api/owner";
import {
  OwnerActionsTypes,
  IAddPartner,
  IDeletePartner,
  IAddCard,
  IDeleteCard,
  setPartners,
  setCards,
} from "./actionCreators";

export function* fetchPartners(): any {
  try {
    const data = yield call(API_OWNER.fetchPartners);
    yield put(setPartners(data));
  } catch (e) {
    errorHandler(e);
  }
}
export function* addPartner({ payload: values }: IAddPartner): any {
  try {
    const data = yield all([yield call(API_OWNER.addPartner, values), yield call(API_OWNER.fetchPartners)]);

    yield put(setPartners(data[1]));
  } catch (e) {
    errorHandler(e);
  }
}
export function* deletePartner({ payload: values }: IDeletePartner): any {
  try {
    yield call(API_OWNER.deletePartner, values);
  } catch (e) {
    errorHandler(e);
  }
}

export function* fetchCards(): any {
  try {
    const data = yield call(API_OWNER.fetchCards);
    yield put(setCards(data));
  } catch (e) {
    errorHandler(e);
  }
}
export function* addCard({ payload: values }: IAddCard): any {
  try {
    const data = yield all([yield call(API_OWNER.addCard, values), yield call(API_OWNER.fetchCards)]);

    yield put(setCards(data[1]));
  } catch (e) {
    errorHandler(e);
  }
}
export function* deleteCard({ payload: values }: IDeleteCard): any {
  try {
    yield call(API_OWNER.deleteCard, values);
  } catch (e) {
    errorHandler(e);
  }
}

export function* ownerSaga(): SagaIterator {
  yield takeEvery(OwnerActionsTypes.FETCH_PARTNERS, fetchPartners);
  yield takeEvery(OwnerActionsTypes.ADD_PARTNER, addPartner);
  yield takeEvery(OwnerActionsTypes.DELETE_PARTNER, deletePartner);

  yield takeEvery(OwnerActionsTypes.FETCH_CARDS, fetchCards);
  yield takeEvery(OwnerActionsTypes.ADD_CARD, addCard);
  yield takeEvery(OwnerActionsTypes.DELETE_CARD, deleteCard);
}
