import { FC, memo, ReactElement } from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import clsx from "clsx";

import { makeStyles, Drawer, List, Divider, IconButton, ListItem, ListItemIcon, ListItemText } from "@material-ui/core";
import { ChevronLeft } from "@material-ui/icons";
import Logo from "assets/img/logo.svg";

import { selectUser } from "store/ducks/manage/selectors";

import { menuItem, MENU_ADMIN, MENU_MODERATOR, MENU_PARTNER } from "./constants";

interface Props {
  open: boolean;
  handleDrawerClose: () => void;
  routeName?: string;
}

const useStyles = makeStyles(theme => ({
  toolbarIcon: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: "0 8px",
    ...theme.mixins.toolbar,
  },
  logo: {
    maxWidth: "160px",
  },
  drawerPaper: {
    position: "relative",
    whiteSpace: "nowrap",
    width: 240,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: "hidden",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9),
    },
  },
  content: {
    flexGrow: 1,
    height: "100vh",
    overflow: "auto",
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  link: {
    color: "inherit",
  },
}));

const Sidebar: FC<Props> = ({ open, handleDrawerClose, routeName }): ReactElement => {
  const s = useStyles();
  const user = useSelector(selectUser);
  const menuItems =
    user.type === 3 ? MENU_ADMIN : user.type === 2 ? MENU_MODERATOR : user.type === 1 ? MENU_PARTNER : [];

  return (
    <Drawer
      variant="permanent"
      classes={{
        paper: clsx(s.drawerPaper, !open && s.drawerPaperClose),
      }}
      open={open}>
      <div className={s.toolbarIcon}>
        <img className={s.logo} src={Logo} alt="logo" />
        <IconButton onClick={handleDrawerClose}>
          <ChevronLeft />
        </IconButton>
      </div>
      <Divider />
      <List>
        {menuItems.map((item: menuItem, i: number) => (
          <Link to={item.path} className={s.link} key={i}>
            <ListItem button selected={routeName === item.title}>
              <ListItemIcon>
                <item.icon />
              </ListItemIcon>
              <ListItemText primary={item.title} />
            </ListItem>
          </Link>
        ))}
      </List>
    </Drawer>
  );
};

export default memo(Sidebar);
