import axios from "plugins/axios";
import {
  IAddShop,
  IChangeNameWithEmail,
  IChangePassword,
  IDeleteShop,
  IEditShop,
  IRefreshKey,
} from "store/ducks/manage/actionCreators";
import { IManage, IShop } from "store/ducks/manage/state";
import { API_URL } from "./constants";

export const API_MANAGE = {
  async changeNameWithEmail({
    name,
    email,
  }: IChangeNameWithEmail["payload"]): Promise<{
    success: boolean;
    user: {
      id: number;
      name: string;
      email: string;
      balance: number;
      createDate: number;
    };
  }> {
    const response = await axios.post(`${API_URL}/manage/editProfile`, { name, email });
    return response.data.user;
  },
  async changePassword({ oldPassword, newPassword }: IChangePassword["payload"]): Promise<{}> {
    const response = await axios.post(`${API_URL}/manage/editProfile`, { oldPassword, newPassword });
    return response.data.user;
  },

  async fetchUser(): Promise<IManage["user"]> {
    const response = await axios.get(`${API_URL}/manage/getUserInfo`);
    return response.data.user;
  },
  async fetchShops(): Promise<IShop[]> {
    const response = await axios.get(`${API_URL}/manage/getShopsList`);
    return response.data.shops;
  },
  async addShop({ name, siteURL }: IAddShop["payload"]): Promise<IShop> {
    const response = await axios.post(`${API_URL}/manage/addShop`, { name, siteURL });
    return response.data.shop;
  },
  async editShop({ id, name, failURL, successURL, webhookURL }: IEditShop["payload"]): Promise<IShop> {
    const response = await axios.post(`${API_URL}/manage/editShop`, {
      id,
      name,
      failURL,
      successURL,
      webhookURL,
    });
    return response.data.shop;
  },
  async deleteShop(id: IDeleteShop["payload"]): Promise<{ success: boolean }> {
    const response = await axios.post(`${API_URL}/manage/removeShop`, { id });
    return response.data.success;
  },

  async refreshKey({ id, value }: IRefreshKey["payload"]): Promise<any> {
    const response = await axios.post(`${API_URL}/manage/updateShopKeys`, { id, value });
    return {
      id,
      [value]: response.data[value],
    };
  },
};
